!(function(window, document) {
  function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }
  var currentPrice = 34.56;
  setInterval(function (argument) {
    var num = getRandomInt(1, 2);
    if (num == 2) {
      currentPrice += .01;
    } else {
      currentPrice -= .01;
    }
  }, 2000);
})(window, document);
